



















import Vue from "vue";
import { Prop, Watch, Component } from "vue-property-decorator";

@Component
export default class Main extends Vue {
    created() {
    }
    percent = 0;
    isLvlUp = false;
    startFill() {
        if (this.percent > 99) this.percent = 0;
        this.fill();
    }

    fill() {
        setTimeout(() => {
            if (this.percent < 100) {
                this.percent++;
                this.fill();
            }
            else this.lvlUp();
        },20)
    }

    lvlUp() {
        setTimeout(() => {
            this.barItems.forEach(bar => bar.isLighted = true)
            this.isLvlUp = true;
            setTimeout(() => {
                this.barItems.forEach(bar => bar.isDim = true)
            }, 300)
        }, 300)
    }

    @Watch("isLvlUp")
    onLvlUp() { setTimeout(() => { this.isLvlUp = false; }, 2000) }
    @Watch("percent")
    onPercentChange() {
        let filledBars = this.percent / 5;
        if (this.percent % 5 == 0) {
            let bar = this.barItems[filledBars-1];
            bar.isDim = false;
            bar.isLighted = true;
        }
    }
    @Watch("barItems", {deep: true})
    onBarItemsChange() {
        console.log("onBarItemsChange")
        if (this.barItems.some(x => x.isLighted)) {
            let items = this.barItems.filter(x => x.isLighted);
            setTimeout(() => {
                for (var item of items) {
                    item.isLighted = false;
                };
            }, 200)
        }
    }

    barItems = [
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'pink', isLighted: false, isDim: true },
        { color: 'yellow', isLighted: false, isDim: true },
        { color: 'yellow', isLighted: false, isDim: true },
        { color: 'yellow', isLighted: false, isDim: true },
        { color: 'yellow', isLighted: false, isDim: true },
        { color: 'yellow', isLighted: false, isDim: true },
        { color: 'lime', isLighted: false, isDim: true },
        { color: 'lime', isLighted: false, isDim: true },
        { color: 'lime', isLighted: false, isDim: true },
        { color: 'lime', isLighted: false, isDim: true },
        { color: 'lime', isLighted: false, isDim: true }
    ]
}
